.header {
  background-color: #282c34;
  color: white;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 2;
 &__cont{
   align-items: center;
 }
  &__img, &__basket-btn {
    height: 5vh;
    margin: 10px 0;
    position: relative;
    cursor: pointer;
  }

  &__basket {
    &-counter {
      display: flex;
      justify-content: center;
      position: absolute;
      width: 20px;
      height: 20px;
      background: rgb(193 7 7);
      border-radius: 50%;
      border: none;
      top: -5px;
      right: -5px;
      font-size: 12px;
      color: white;
    }

    &-btn {
      border: none;
      background: none;
    }
  }
}

.logo{
  &__cont {
    align-items: center;
    cursor: pointer;
  }
  &__img {
    height: 5vh;
    margin: 10px 0;
    position: relative;
    cursor: pointer;
  }
  &__text {
    font-size: 36px;
    margin-left: 10px;

    &:hover {
      color: yellowgreen;
    }
  }
}

.logo, .header__basket-img {
  height: 100%;
}