.basket {
  padding: 20px;
  text-align: center;

  &__card {
    justify-content: space-between;
    box-shadow: 0 0 7px 1px;
    padding: 5px 15px;
    background: #ffffffe3;

    &:not(:first-child) {
      margin-top: 5px;
    }

    &-justify {
      margin-right: 20px;
      justify-content: space-between;
      width: 100%;
    }
  }

  &__img {
    height: 100%;

    &-box {
      height: 5vh;
    }
  }

  &__btn {
    width: 10%;
    padding: 5px;
    border: 1px solid black;
    border-radius: 5px;
    background: #d8edff;
    cursor: pointer;

    &:hover {
      background: darkred;
    }
  }
  &__title, &__sum{
    font-size: 20px;
  }
  &__link {
    font-size: 18px;
    text-decoration: underline;
    color: blue;
    cursor: pointer;

    &:hover {
      color: #6b8d25;
    }
  }
}

.clear-btn {
  background: #6b8d25;
  font-size: 20px;
  border-radius: 25px;
  color: white;
  margin: 10px 0;
  padding: 5px 0;
  cursor: pointer;
  &:hover{
    background: darkred;
  }
}
.del-button{
  height: 2.5rem;
  width: 2.5rem;
}
.count_btn {
  height: 1.5rem;
  width: 1.5rem;
}
.count, .price {
  padding: 0 0.5rem;
  font-size: 18px;
}
.del-button, .count_btn {
  border: none;
  background: none;
  cursor: pointer;
  &-img {
    width: 100%;
  }
}

.message {
  font-size: 48px;
  margin: 20px 0;
  cursor: default;
}

.sum{
  padding: 15px;
  &__price{
    margin-right: 4%;
    color: darkred;
  }
  &__title, &__price{
    font-size: 22px;
    font-weight: bold;
  }
}
